@mixin panel {
    background: var(--white-color);
    box-shadow: 0 0.16rem 0.66rem var(--grey-shadow-color);
    border-radius: 0.416rem;
}

@mixin desktop {
    @media (width >= 768px) {
        @content;
    }
}

@mixin mobile {
    @media (width < 768px) {
        @content;
    }
}